import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';

@Injectable()
export class ConfigurationRepositoryNavigationFactory implements NavigatorNodeFactory {
  node: NavigatorNode;

  constructor() {
    this.node = new NavigatorNode({
      label: gettext('Configuration repository'),
      path: 'configuration',
      icon: 'gears',
      parent: gettext('Management'),
      priority: 800
    });
  }

  get(): NavigatorNode {
    return this.node;
  }
}
